























































































































































































































































































import getRepository from '@/services'
import { Component, Prop, Mixins } from 'vue-property-decorator'
import ContractRepository from '@/services/repositories/contract'
import { namespace } from 'vuex-class'
import PopupMixin from '@/mixins/popup'
import { RELATIONSHIP_VALUE_TO_TYPE } from '@/configs'
import ParamRepository from '@/services/repositories/params'
const beUser = namespace('beUser')

@Component({ components: {} })
export default class DetailContract extends Mixins(PopupMixin) {
  @beUser.State('user') user!: any
  @beUser.State('role') role!: Record<string, any>

  @Prop({ required: false, type: Object, default: {} }) detail!: Record<string, any>
  @Prop({
    required: false,
    type: Object,
    default: () => {
      return {}
    }
  })
  dataEdit!: Record<string, any>
  @Prop() dataPopupconfirm!: any
  query = {
    page: 1,
    limit: 20
  }
  showAction = true
  contract: Record<string, any> = {}
  apiContract!: ContractRepository
  apiParam!: ParamRepository
  contractAction: Record<string, any> = {}
  params: any = {}
  listConsultantCode = []
  fileType = ''
  get iconFileType(): string {
    let name = ''
    if (this.fileType.includes('doc') || this.fileType.includes('docx')) {
      name = 'word'
    }
    if (this.fileType.includes('pdf')) {
      name = 'pdf'
    }
    if (this.fileType.includes('xls')) {
      name = 'excel-fullcolor'
    }
    if (this.fileType.includes('zip')) {
      name = 'zip'
    }
    return name
  }
  get disableCancelContract(): boolean {
    let now = new Date()
    let check = false
    if (this.contract.allowCancelDate) {
      let allowCancelDate = new Date(this.contract.allowCancelDate)
      check = allowCancelDate > now
    } else {
      check = true
    }
    let checkRoute = this.$route.name === 'contract' || this.$route.name === 'audit' || this.$route.name !== 'cancel' ? true : false
    if (checkRoute && this.contract.contractStatus !== '2' && check) {
      return true
    }
    return false
  }
  get disable(): boolean {
    if (this.$route.name === 'audit' && this.contract.auditStatus === '1') {
      return true
    }
    return false
  }
  get disableEditContract(): boolean {
    if (this.contract.auditStatus === '1' || this.contract.auditStatus === '8') {
      return true
    }
    return false
  }
  get getReason(): string {
    let reason = ''
    if (this.contract.changeConsultantCode == '2') {
      reason = 'NV đứng code thay đổi Phòng giao dịch'
    } else {
      reason = 'NV đứng code nghỉ việc'
    }
    return reason
  }
  async handleApprove(type: string): Promise<any> {
    await this.apiContract
      .changeConsultant(this.contract.bankContractNo, {
        code: this.contract.changeConsultantCode,
        status: type == 'reject' ? 3 : 2,
        reason: this.contract.changeConsultantReason,
        attachmentUrl: this.contract.changeConsultantAttachment
      })
      .then(() => {
        this.$message.success(`${type == 'reject' ? 'Từ chối yêu cầu' : 'Duyệt yêu cầu'} thành công`)
        this.$emit('reset')
        this.setOpenPopup({
          popupName: 'contractDetail',
          isOpen: false
        })
      })
      .catch(err => this.$message.error(err.message ? err.message : type == 'reject' ? 'Từ chối yêu cầu thất bại' : 'Duyệt yêu cầu thất bại'))
  }
  checkBetweenDay(day: string | number): boolean {
    const time = 8.64e7
    const timeTomorrow = new Date(day).getTime() + time
    return new Date().getTime() >= new Date(day).getTime() && new Date().getTime() <= timeTomorrow
  }

  get checkSaleMember(): boolean {
    const isOfficeMember = this.role.userRole.length === 1 && this.role.userRole.find((role: string) => role === 'TRANSACTION_OFFICE_MEMBER')
    console.log('isOfficeMember', isOfficeMember)
    console.log(' this.contract.createdBy === this.user.id', this.contract.createdBy === this.user.id)
    console.log(' this.contract.salesMemberId !== this.user.memberId', this.contract.salesMemberId !== this.user.memberId)
    console.log('!this.checkBetweenDay', !this.checkBetweenDay(this.contract.createdAt))

    // if user là người tạo hộ người khác, và trong vòng 24h thì dc sửa (phủ định func checkBetween)
    if (isOfficeMember && this.contract.createdBy === this.user.id && this.contract.salesMemberId !== this.user.memberId && !this.checkBetweenDay(this.contract.createdAt)) {
      return false
    }
    return true
  }

  handleOpen(): void {
    if (this.$route.name === 'pending') {
      this.params.searchType = 'APPLY_FORM'
      this.params.contractStatus = '3'
      this.showAction = false
    } else if (this.$route.name === 'refuse') {
      this.params.searchType = 'APPLY_FORM'
      this.params.contractStatus = '4'
      this.showAction = false
    } else if (this.$route.name === 'lapse') {
      this.params.searchType = 'CONTRACT'
      this.params.contractStatus = '4'
      this.showAction = false
    } else if (this.$route.name === 'new') {
      this.params.searchType = 'CONTRACT'
      this.params.contractStatus = '1'
    } else if (this.$route.name === 'cancel') {
      this.params.searchType = 'CONTRACT'
      this.params.contractStatus = '2'
    }
    if (
      this.$route.name == 'dang-doi-soat' ||
      this.$route.name == 'cho-duyet' ||
      this.$route.name == 'da-duyet' ||
      this.$route.name == 'yeu-cau-hieu-chinh' ||
      this.$route.name == 'da-khop'
    ) {
      this.showAction = false
    }
    this.apiContract
      .getDataDetail(this.detail?.bankContractNo, this.params)
      .then((response: any) => {
        this.contract = response
      })
      .catch((err: any) => console.log(err))
    this.apiParam.getChangeConsultantCode().then(res => (this.listConsultantCode = res))
  }
  resetSelectContract(): void {
    this.apiContract
      .getDataDetail(this.detail?.bankContractNo, this.params)
      .then((response: any) => {
        this.contract = response
      })
      .catch((err: any) => console.log(err))
    this.$emit('reset')
  }
  handleClose(): void {
    console.log('close test popup')
    this.fileType = ''
  }
  getFileNameByUrl(url: string): string {
    if (url) {
      const fileNameUrl = url.split('/').reverse()[0].toString()
      this.fileType = fileNameUrl.split('.')[1].toString()
      return fileNameUrl
    }
    return ''
  }
  indexMethod(index: number): number {
    return (this.query.page - 1) * this.query.limit + index + 1
  }
  async handleConfirm(): Promise<any> {
    const ids = this.dataPopupconfirm.ids
    await this.apiContract
      ?.deleteContract({ ids: ids })
      .then(() => {
        this.$message({
          type: 'success',
          message: 'Hủy thành công'
        })
        this.setOpenPopup({
          popupName: 'popup-confirm',
          isOpen: false
        })
        this.$emit('reset')
      })
      .catch(() =>
        this.$message({
          type: 'warning',
          message: 'Hủy thất bại'
        })
      )
  }
  handleClickActionDetail(command: string): void {
    if (command == 'row') {
      this.setOpenPopup({
        popupName: 'actionContract',
        isOpen: true
      })
    }
    if (command == 'destroy') {
      this.$emit('openDestroy', { status: 'destroy-detail', row: this.contract })
    }
    if (command == 'edit') {
      this.dataEdit.code = command
      this.dataEdit.bankContractNo = this.detail.bankContractNo
      this.setOpenPopup({
        popupName: 'create-cost',
        isOpen: true
      })
    }
    if (command == 'history') {
      this.setOpenPopup({ popupName: 'edit-history', isOpen: true })
    }
    if (command == 'change-staff-code') {
      this.setOpenPopup({
        popupName: 'change-staff-code',
        isOpen: true
      })
    }
  }
  getRelationShip(code: number): string {
    return RELATIONSHIP_VALUE_TO_TYPE[code]
  }
  getPaymentPeriod(): void {
    let paymentPeriod: any = ''
    if (this.contract.paymentPeriod == 1) {
      paymentPeriod = '1 năm'
    }
    if (this.contract.paymentPeriod == 2) {
      paymentPeriod = 'Nửa năm'
    }
    if (this.contract.paymentPeriod == 4) {
      paymentPeriod = 'Quý'
    }
    return paymentPeriod
  }

  created(): void {
    this.apiContract = getRepository('contract', {
      districtId: this.user?.districts?.[0].id
    })
    this.apiParam = getRepository('param', {
      districtId: this.user?.districts?.[0].id
    })
  }
}
